/** Represents a `robots.txt` file entity that controls web crawler access to a website or subdomain. It defines crawling rules and directives for search engines and other automated agents, ensuring proper indexing behavior and preventing unauthorized access to specific site areas. */
export interface RobotsTxt {
  /**
   * Full text content of the `robots.txt` file.
   * @maxLength 720000
   */
  content?: string;
  /** Whether this `robots.txt` file uses Wix's [default content](https://support.wix.com/en/article/editing-your-sites-robotstxt-file). */
  default?: boolean;
  /**
   * If applicable, target subdomain for the `robots.txt` file. For example, a site can have multiple subdomains like `www.example.com`, `es.example.com`, `fr.example.com`. Each subdomain can have its own `robots.txt` file with different rules. Default: 'www'
   * @maxLength 63
   */
  subdomain?: string;
}

/** The request to get the Robots.txt file content */
export interface GetRobotsTxtRequest {
  /** Subdomain of the requested `robots.txt file`. For example, `www`, `es`, `fr`. Default: `www`. */
  subdomain?: string;
}

/** The response of the Robots.txt file request */
export interface GetRobotsTxtResponse {
  /** Retrieved `robots.txt` file entity. */
  robotsTxt?: RobotsTxt;
}

/** The request to update the content of the Robots.txt file */
export interface UpdateRobotsTxtRequest {
  /** `robots.txt` file to update. */
  robotsTxt?: RobotsTxt;
}

export interface UpdateRobotsTxtResponse {
  /** Updated `robots.txt` file. */
  robotsTxt?: RobotsTxt;
}

